<script>
import html2canvas from "html2canvas";
import JsPDF from "jspdf";
export default {
  name: "Protocol",
  data() {
    return {
      registno: "REGISTNO1234",
      protocolInfo: {
        //
        claimId: "1",
        realname: "",
        sex: "",
        age: "21",
        idcard: "1321234234233323534",
        mobile: "142343434453",
        ac_year: "2020",
        ac_month: "12",
        ac_day: "12",
        pro_year: "2021",
        pro_month: "08",
        pro_day: "08",
      },
      secondPerson: {
        //
        claimId: "1",
        realname: "",
        idcard: "123456789012345678",
        mobile: "12345678901",
        sex: "",
        age: "19",
      },
      thirdPerson: {
        //
        claimId: "1",
        realname: "",
        idcard: "876543210987654321",
        mobile: "10987654321",
        sex: "",
        age: "20",
      },
      flag1: true,
      flag2: true,
      contentText: "", //
      projectText: "", //
    };
  },
  created() {},
  methods: {
    generate: function () {
      var target = document.getElementById("biaoge_pdf");
      var claimId = [];
      if (
        claimId.indexOf(this.protocolInfo.claimId) < 0 &&
        this.protocolInfo.claimId
      ) {
        claimId.push(this.protocolInfo.claimId * 1);
      }
      if (
        claimId.indexOf(this.secondPerson.claimId) < 0 &&
        this.secondPerson.claimId
      ) {
        claimId.push(this.secondPerson.claimId * 1);
      }
      if (
        claimId.indexOf(this.thirdPerson.claimId) < 0 &&
        this.thirdPerson.claimId
      ) {
        claimId.push(this.thirdPerson.claimId * 1);
      }
      claimId = JSON.stringify(claimId);
      var len1 = this.projectText.length;
      var len2 = this.contentText.length;
      if (len1 > 0) {
        this.flag1 = false;
      }
      if (len2 > 0) {
        this.flag2 = false;
      }
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        closeOnClickModal: false,
        closeOnPressEscape: false,
        closeOnHashChange: false,
        type: "warning",
      })
        .then(() => {
          this.utilpdf(target, claimId);
          // this.$message({
          //     type: 'success',
          //     message: '!'
          // });
          this.flag1 = true;
          this.flag2 = true;
        })
        .catch(() => {
          this.flag1 = true;
          this.flag2 = true;
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    utilpdf: function (ele, claimId) {
      let eleW = ele.offsetWidth; // 
      let eleH = ele.offsetHeight; // 
      let eleOffsetTop = ele.offsetTop; // 
      let eleOffsetLeft = ele.offsetLeft; // 

      var canvas = document.createElement("canvas");
      var abs = 0;

      let win_in =
        document.documentElement.clientWidth || document.body.clientWidth; // 
      let win_out = window.innerWidth; // 

      if (win_out > win_in) {
        // abs = (win_o - win_i)/2;    // 
        abs = (win_out - win_in) / 2; // 
        // console.log(a, 'abs');
      }

      var opts = {
        scale: 3, //
        dpi: 300,
        // allowTaint: true,  // canvas  allowTainttoDataURLcanvas
        useCORS: true, //canvas , 
      };

      canvas.width = eleW * 8; // &&
      canvas.height = eleH * 8;

      var context = canvas.getContext("2d");
      context.scale(2, 2);
      context.translate(-eleOffsetLeft - abs, -eleOffsetTop);
      // offset.left(),
      // translate
      html2canvas(ele, opts).then((canvas) => {
        var contentWidth = canvas.width;
        var contentHeight = canvas.height;
        //pdfhtmlcanvas;
        var pageHeight = (contentWidth / 592.28) * 841.89;
        //pdfhtml
        var leftHeight = contentHeight;
        //
        var position = 0;
        //a4[595.28,841.89]htmlcanvaspdf
        var imgWidth = 595.28;
        var imgHeight = (595.28 / contentWidth) * contentHeight;
        var pageData = canvas.toDataURL("image/jpeg", 1.0);
        var pdf = new JsPDF("p", "pt", "a4");
        //htmlpdf(841.89)
        //pdf
        if (leftHeight < pageHeight) {
          //pdf.addImage(pageData, 'JPEG', )pdf
          pdf.addImage(pageData, "JPEG", 0, 0, imgWidth, imgHeight);
          // pdf.addImage(pageData, 'JPEG', 20, 40, imgWidth, imgHeight);
        } else {
          // 
          while (leftHeight > 0) {
            pdf.addImage(pageData, "JPEG", 0, position, imgWidth, imgHeight);
            leftHeight -= pageHeight;
            position -= 841.89;
            //
            if (leftHeight > 0) {
              pdf.addPage();
            }
          }
        }
        // :
        var pdfName = ".pdf";
        pdf.save(pdfName);

        // : 
        /*formData.append()
                FileBlob
                formData.append()Blob,,'blob'
                */

        /* 
                var datapdf = pdf.output("blob");
                var formData = new FormData();
                formData.append("file", datapdf,'.pdf');
                formData.append("claimId", claimId);
                this.$axios.post('/Base/generatePdf',formData)
                .then(data => {
                    if (data.data.code == 200) {
                        this.$message({
                            type: "success",
                            message: ""
                        });
                    } else {
                        this.$message.error(data.msg);
                    }
                })
                .catch(err => {
                    this.$message.error(err);
                }); 
                */
      });
    },
  },
};
</script>

